// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
const ToggleLight = () => {
  const { isLight, setLight } = useContext(ThemeContext);
  return (
    <>
      <Button
        type='button'
        aria-label='Toggle light'
        onClick={() => setLight(!isLight)}
        islight={isLight}
      >
        {isLight ? Moon : Sun}
      </Button>
    </>
  );
};

export default ToggleLight;
